.cruises {
  width: auto;
  box-sizing: border-box;
  padding: 115px 0 120px;
}

.cruises__title {
  font-weight: 600;
  font-size: 28px;
  margin: 0;
  margin-bottom: 23px;
}

.cruises__list {
  max-width: 1366px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;

  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0 auto;
}

.cruises__item {
  box-sizing: border-box;
  width: calc(100% / 4 - 30px);
  max-width: 260px;
  text-align: left;
  margin: 31px 15px;

  background-color: #638ec3;
  background-size: 100% 100%;
  background-repeat: no-repeat;

  &--astoria {
    background-image: url("../img/cruises/astoria-desktop@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/cruises/astoria-desktop@2x.jpg");
    }
  }

  &--elain {
    background-image: url("../img/cruises/elain-desktop@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/cruises/elain-desktop@2x.jpg");
    }
  }

  &--atlantis {
    background-image: url("../img/cruises/atlantis-desktop@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/cruises/atlantis-desktop@2x.jpg");
    }
  }

  &--odyssey {
    background-image: url("../img/cruises/odyssey-desktop@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/cruises/odyssey-desktop@2x.jpg");
    }
  }

  &--olympia {
    background-image: url("../img/cruises/olympia-desktop@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/cruises/olympia-desktop@2x.jpg");
    }
  }

  &--aurora {
    background-image: url("../img/cruises/aurora-desktop@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/cruises/aurora-desktop@2x.jpg");
    }
  }

  &--leonor {
    background-image: url("../img/cruises/leonor-desktop@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/cruises/leonor-desktop@2x.jpg");
    }
  }

  &--minion {
    background-image: url("../img/cruises/minion-desktop@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/cruises/minion-desktop@2x.jpg");
    }
  }

  &:hover {
    .cruises__card {
      opacity: 1;
    }

    .cruises__item-title {
      opacity: 0;
    }
  }
}

.cruises__item-title,
.cruises__card-title {
  font-weight: 500;
  font-size: 18px;

  margin: 0;
}

.cruises__card-title {
  padding-bottom: 15px;
}

.cruises__item-title {
  color: $main;
  position: relative;
  top: 340px;
  left: 30px;
}

.cruises__card {
  background-color: $back;
  box-sizing: border-box;
  color: $main;
  padding: 34px 23px 34px 25px;
  margin-top: -21px;
  opacity: 0;
  height: 386px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cruises__card-item,
.cruises__card-description {
  font-size: 14px;
  line-height: 20px;
  margin: 0;
  padding: 0;
  text-align: left;
  border: none;
}

.cruises__card-item {
  font-weight: 500;

  &--route {
    font-size: 14px;
    line-height: 20px;
    margin: 0;
  }
}

.cruises__card-description {
  color: $text;
  font-weight: normal;
  margin-top: -1px;
  padding-bottom: 8px;

  &--route {
    letter-spacing: -0.1px;
  }
}

.cruises__booking {
  font-size: 14px;
  line-height: 21px;
  padding: 10px 0;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  color: $back;

  &:hover {
    background-color: $text;
  }
}

.cruises__card-info {
  margin-left: -2px;
  margin-top: -3px;
}


